<template>
    <div>
        <div class="field is-grouped">
            <div class="control mr-1">
                <div class="file is-boxed is-warning">
                    <label class="file-label">
                        <input type="file" class="file-input" accept="image/*" capture="camera" v-on:change="loadImageFileAsURL">
                        <span class="file-cta ">
                            <span class="file-icon">
                                <i class="las la-upload"></i>
                            </span>
                            <span class="file-label">
                                Upload file…
                            </span>
                        </span>
                    </label>
                </div>
            </div>
            <div class="control is-warning">
                <div class="file is-boxed">
                    <label onclick="cameraGo()" class="file-label"><input type="button" class="file-input" value="">
                        <span class="file-cta ">
                            <span class="file-icon">
                                <i class="las la-camera"></i>
                            </span>
                            <span class="file-label">
                                Camera
                            </span>
                        </span></label>
                </div>
            </div>
        </div>
        <div class="field">
            <label>Select image to upload or capture using camera</label>
        </div>
        <div class="field mb-3">
            <figure class="image">
                <img v-bind:src="base64Photo" >
            </figure>
        </div>
    </div>
</template>
<script>
export default {
    name: 'ImagePreview',
    data: function() {
        return {
            name: 'Image Preview',
            base64Photo: '',
            errors: [],
        }
    },
    // props: ['imgID'],
    methods: {
        loadImageFileAsURL: function(e) {
            const filesSelected = e.target.files[0];
            this.getBase64(filesSelected);
        },
        getBase64: function(fileobject) {
            const fileReader = new FileReader();
            fileReader.onload = (e) => {
                this.base64Photo = e.target.result;
            }
            fileReader.readAsDataURL(fileobject);
        }
    }
}
</script>